import React,{useState} from "react";
import Card from "../UI/Card";
import Button from "../UI/Button";
import classes from './addUser.module.css';
import ErrorModal from "../UI/ErrorModal";
function AddUsers(props){
    //array destructuring
    const [enteredUsername, setEnteredUsername] = useState('');
    const [enteredUserAge, setEnteredUserAge] = useState('');
    const [error, setError]=useState('');

    function nameChangeHandler(event){
        setEnteredUsername(event.target.value);
    }

    function ageChangeHandler(event){
        setEnteredUserAge(event.target.value);
    }

function addUserHandler(event){
   // console.log(event.target.value);
    event.preventDefault();
    if(enteredUsername.trim().length === 0 || enteredUserAge.trim().length === 0){
       setError({
        title:"Invalid Input",   
        message:"Please Enter a valid name and an age(non-empty values)"
       });
        console.log('Kindly enter something')
        return;
    }
    /* by putting a  + before the string it will become a number */
    if(+enteredUserAge < 1){
        setError({
            title:"Invalid Age",   
            message:"Please Enter a valid age(>0)"
           });
        return;
    }
    //console.log(enteredUsername,enteredUserAge);
    props.onAddUser(enteredUsername, enteredUserAge);
    /* resetting values to empty to empty the form */
    setEnteredUsername('');
    setEnteredUserAge('');
}
function ErrorHandler(){
         setError(null);   
}
return(  
    <div> 
        {/* if error occurs then render the ErrorModal component */}   
        {error && <ErrorModal onErrorHandler={ErrorHandler} title={error.title} message={error.message}/>}
    <Card className={classes.input}> 
    <form onSubmit={addUserHandler}>
        <label htmlFor="username">Username</label>
        <input id="username" value={enteredUsername} onChange={nameChangeHandler} type="text"/>
        <label htmlFor="age"> Age</label>
        <input id="age" type="text" value={enteredUserAge} onChange={ageChangeHandler}/>
        <Button type="submit">Add User</Button>
    </form>
    </Card>
    </div>
 
    );
};

export default AddUsers;