import React , {useState}from 'react';
import AddUsers from './components/Users/addUsers';
import UserList from './components/Users/UsersList';

function App() {
  const [usersList , setUserList] = useState([]);

  function addUserHandler(uName, uAge){
    setUserList((PreviousUserList) => {
      return [...PreviousUserList,{name:uName, age:uAge,id: Math.random().toString()}]
    });
  }
  return (
    <div>
      {/* onAddUser is the prop which is being used in addUsers.js
        line 30 where username and age is being passed and over here onAddUser
        points to the AddUserHandler function which recieves those values and 
        not only saves the older values but also adds the new values into the list
         */}
      <AddUsers onAddUser={addUserHandler}/>
      <UserList users={usersList} />
    </div>
  );
}

export default App;
